.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  color: var(--dark-color);
}
  
.card__image {
  width: 160px;
  height: 134px;
}
  
.card__product-description {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 40px;
}
  
.card__product-title {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: var(--dark-color);
}
  
.card__product-code {
  font-size: 10px;
  line-height: 14px;
  color: var(--grey-color);
  margin-top: 4px;
}
  
.card__review {
  margin-top: 16px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
  
.card__review-count {
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  display: flex;
  align-items: flex-end;
  text-align: right;
  color: var(--dark-color);
}
  
.card__price {
  margin-top: 24px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
  
.card__price-label {
  line-height: 18px;
  color: var(--grey-color);
}
  
.card__price-amount {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
  
.card__buy-button {
  margin-top: 16px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 40px;
  width: 100%;
  height: 40px;
  color: #fff;
  background-color: var(--blue-color);
  border: 1px solid var(--blue-color);
  border-radius: 5px;
  transition: color var(--transition-time);
}
  
.card__buy-button:hover {
  background-color: #fff;
  color: var(--blue-color);
}
